*{

  box-sizing:border-box !important;
  list-style:none !important  ;
  text-decoration:none !important;
}
.nav-link{
  margin-left: 20px;
  position: relative;
}
.header{
  background-color: white !important;
}
.header.shadow{
  box-shadow: 0 0 3px 4px rgba(0, 0, 0, 0.13);
}
.py-96{
  padding: 96px 0;
}
.slider{
  height: 120vh !important;
  overflow: hidden;
}

.bounce img{
  width: 100%;
  height: 100%;
  object-fit: cover;
  animation: pulse 1s linear infinite alternate;
}
@keyframes pulse {
  0%{
    transform: scale(1.1);
  }
  100%{
    transform: scale(1.2);
  }
}
.sliderTitle{
  font-size: 90px;
  color: rgb(11, 28, 57);
  margin-top: 100px;
  font-weight: 700;
}
.slider-text p{
  color: rgb(11, 28, 57);
}
.btn-Shop{
  display: inline-block;
  padding: 15px 30px;
  background: rgb(74, 74, 75);
  color: white !important;
  position: relative;
  z-index: 0;
  font-weight: 700;
}
.btn-Shop::before{
  content:'';
  position:absolute;
  top:0;
  left:0;
  background-color: red;
  transform: scaleX(0);
  transform-origin: left;
  transition: all  0.4s cubic-bezier(.42,2,.45,.14);
  width:100%;
  height:100%;
  z-index: -1;
}

.btn-Shop:hover::before{
  transform: scaleX(1);
}
.card{
  margin: 20px 0;
  height: 480px !important;
}
.card-img{
  height: 380px;
  position: relative;
  overflow: hidden;
}
.heart{
  position: absolute;
  top: 5px;
  right: 10px;
  width: 50px;
  height: 50px;
  background-color: transparent;
  border: 0;
  color: red;
  font-size: 25px;
}
.heart img{
  width: 100%;
  height: 100%;
}
.addCart{
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translate(-50%,100px);
  transition: 0.4s;
}
.card:hover .addCart{
  transform: translate(-50%,-40%);
}
.card img{
  height:100%;
  object-fit: cover;
  transition: 0.4s;
}
.card:hover img{
transform: scale(1.2);
}
.nav-link{
  position: relative;
}
.service-box{
  transition: 0.4s;
}
.service-box:hover i{
  color: white !important;
}
.service-box:hover{
  background-color: #0D6EFD !important;
  color: white;
}
.Item-Counter{
  display: inline-block;
  position: absolute;
  font-size: 12px;
  transform: translate(3px,-10px);
}
.swiper-button-prev{
  transform: translateX(-10px);
}
.swiper-button-next::after,
.swiper-button-prev::after{
color: red;
}
.swiper-button-next{
  transform: translateX(10px);
}
.back{
  position: relative;
  z-index: 0;
}
.back::before{
  content:'SUBSCRIBE';
  position:absolute;
  color: rgba(255, 255, 255, 0.342);
  font-size: 150px;
  top:0;
  left:0;
  transform: translateY(-120%);
  width:100%;
  height:100%;
  z-index: -1;
}
.allPro-bg{
  background-image:url("https://cdn.shopify.com/s/files/1/0074/5432/6835/products/electricianz-soprano-z-2_1800x1800.jpg?v=1615930744");
  background-size:cover;
  background-position:center;
  background-repeat:no-repeat;
}

.CartPage-bg{
  background-image:url('https://cdn.shopify.com/s/files/1/0074/5432/6835/files/xeric-halograph-ii-automatic-arctic-blue-1_900x.jpg?v=1645569341');
  background-size:cover;
  background-position:center;
  background-repeat:no-repeat;
}
.breadcrumb-item::before{
  color: white !important;
}
.error{
  position: absolute;
  bottom: 0;
  transform: translate(5px,20px);
  color: red;
  display: block;
  font-size: 13px;
}

.ShopCartImg{
  width:320px;
  height: 150px;
  object-fit: cover;
}

.totalPrice{
  width: 100% !important;
  word-break: break-all;
}
input[type=number]::-webkit-outer-spin-button,
input[type=number]::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
.goto{
  display: inline-block;
  background-color: #0D6EFD;
  color: white !important;
  width: 200px;
  margin-top: 20px;
  padding: 10px 30px;
  border-radius: 10px;
}
@media (max-width:769px){
  .sliderTitle{
    font-size: 40px;
  }
  .addCart{
    font-size: 16px;
    padding:10px 20px  !important;
  }
  .back::before{
    display: none;
  }
}
